import React, { useMemo } from "react";
import { List, AutoSizer } from "react-virtualized";
import Table from "react-bootstrap/Table";
import "./ErrorPopup.css";
import ReusableAlert from "../Alert/ReusableAlert";

const ErrorPopup = ({ errors }) => {
  // Memoize error grouping to prevent unnecessary recalculations
  const groupedErrors = useMemo(() => {
    const errorMap = new Map();

    errors.forEach((error) => {
      // Extract row number and column name using regex
      const rowMatch = error.match(/Row (\d+):/);
      const columnMatch = error.match(/"([^"]*)" is required/);

      const rowNum = rowMatch ? rowMatch[1] : ""; // Get row number or default to "-"
      // If columnMatch exists, use the captured column name, else check if it's a Collaborative Space error
      const columnName = columnMatch
        ? columnMatch[1]
        : error.includes("Collaborative Space")
        ? "Collaborative Space"
        : "N/A";
      const errorMsg = rowMatch ? error.split(": ")[1] : error; // Extract error message
      // Initialize array for errors if row number is not already in the Map
      if (!groupedErrors.has(rowNum)) {
        groupedErrors.set(rowNum, []);
      }
      // Add error details to the groupedErrors Map
      groupedErrors.get(rowNum).push({ errorMsg, columnName });
    });

    return errorMap;
  }, [errors]);

  // Row renderer for virtualized list
  const rowRenderer = ({ index, key, style }) => {
    const entries = Array.from(groupedErrors.entries());
    const [rowNum, errorDetails] = entries[index];

    return (
      <div key={key} style={style} className="virtual-row">
        {errorDetails.map((error, errorIndex) => (
          <div key={`${rowNum}-${errorIndex}`} className="error-entry">
            {errorIndex === 0 && <div className="row-number">{rowNum}</div>}
            <div className="column-name">{error.columnName}</div>
            <div className="error-description">{error.errorMsg}</div>
          </div>
        ))}
      </div>
    );
  };

  return (
    <>
      <ReusableAlert
        variant="danger"
        message={
          <strong>
            ⚠️ Note: Below reports show only uploaded spreadsheet error rows. To
            resolve these issues, update the spreadsheet and re-import.
          </strong>
        }
        className="custom-alert"
      />
      <div className="error-table-container" style={{ height: "400px" }}>
        <AutoSizer>
          {({ height, width }) => (
            <List
              width={width}
              height={height}
              rowCount={groupedErrors.size}
              rowHeight={50}
              rowRenderer={rowRenderer}
            />
          )}
        </AutoSizer>
      </div>
    </>
  );
};

export default ErrorPopup;
