import * as XLSX from "xlsx";
import config from "./config.json";
import { validateDataInBatches } from "./validateDataInBatches";

const validateFile = (file, collabSpaceTitles, selectedOperation) => {
  return new Promise((resolve, reject) => {
    console.log("Starting file validation for:", file.name, selectedOperation);
    console.log("Selected Operation:", selectedOperation);
    console.log("File Name:", file.name);
    console.log(
      "[validateFile.js] Required Collaborative Space:",
      collabSpaceTitles
    );

    const operationNames = {
      1: "Physical Product",
      2: "Physical Product Structure",
      3: "Document",
      4: "Physical Product-Document",
    };

    if (!selectedOperation) {
      reject({
        errors: ["Please select an operation first."],
        headers: [],
      });
      return;
    }

    // 🔍 Ensure collabSpaceTitles is an array
    if (!Array.isArray(collabSpaceTitles)) {
      console.error(
        "[validateFile.js] ❌ collabSpaceTitles is not an array!",
        collabSpaceTitles
      );
      reject({
        errors: ["Internal error: Invalid Collaborative Space list."],
        headers: [],
      });
      return;
    }

    console.log(
      "[validateFile.js] Required Collaborative Spaces:",
      collabSpaceTitles
    );

    const reader = new FileReader();
    reader.readAsBinaryString(file);

    reader.onload = async (e) => {
      try {
        console.log("File successfully read. Parsing...");

        const workbook = XLSX.read(e.target.result, { type: "binary" });
        const sheetName = workbook.SheetNames[0];
        console.log("Detected sheet name:", sheetName);

        const worksheet = workbook.Sheets[sheetName];

        const headers =
          XLSX.utils.sheet_to_json(worksheet, { header: 1 })[0] || [];
        console.log("Extracted headers:", headers);

        if (!headers || headers.length === 0) {
          console.error("❌ No headers found in the file.");
          reject({
            errors: ["The uploaded file has no content or is empty."],
            headers: [],
          });
          return;
        }

        // ✅ Convert worksheet into JSON (with empty cell values captured)
        const sheetData = XLSX.utils.sheet_to_json(worksheet, { defval: "" });
        console.log("Extracted sheet data:", sheetData);

        if (sheetData.length === 0) {
          console.error("❌ No row data found. Only headers exist.");
          reject({
            errors: [
              "The uploaded file contains only headers but no data. Please add at least one row.",
            ],
            headers,
          });
          return;
        }

        // ✅ Extract headers from the first data row
        const fileHeaders = Object.keys(sheetData[0]);
        console.log("Parsed file headers:", fileHeaders);

        // Get expected headers for selected operation
        const expectedHeadersForOperation =
          config.MassUpload[operationNames[selectedOperation]]?.fileHeaders;
        console.log(
          "Expected headers for selected operation:",
          expectedHeadersForOperation
        );

        if (!expectedHeadersForOperation) {
          console.error(
            "❌ No template configuration found for selected operation"
          );
          reject({
            errors: ["Invalid operation selected."],
            headers: [],
            isTemplateMismatch: true,
          });
          return;
        }

        // Check if headers match the selected operation's template
        const headersMismatch = !expectedHeadersForOperation.every((header) =>
          fileHeaders.includes(header)
        );

        if (headersMismatch) {
          console.error("❌ Template does not match selected operation");
          reject({
            errors: [
              `The uploaded template does not match the selected operation: ${operationNames[selectedOperation]}`,
            ],
            headers: fileHeaders,
            isTemplateMismatch: true,
          });
          return;
        }

        // 🔍 **Step 1: Find Matching Operation Based on Headers**
        let matchedOperation = null;
        for (const [key, value] of Object.entries(config.MassUpload)) {
          if (
            value.fileHeaders.every((header) => fileHeaders.includes(header))
          ) {
            matchedOperation = key;
            console.log("Matched operation:", matchedOperation);
            break;
          }
        }
        if (!matchedOperation) {
          console.error("❌ No matching operation found for given headers.");
          reject({
            errors: ["File headers do not match any expected template."],
            headers,
          });
          return;
        }

        // ✅ **Step 2: Validate Headers**
        const expectedHeaders = config.MassUpload[matchedOperation].fileHeaders;
        console.log("Expected Headers:", expectedHeaders);
        const mandatoryAttributes =
          config.MassUpload[matchedOperation].mandatoryAttributes;
        console.log("Mandatory Attributes:", mandatoryAttributes);

        // Replace the existing validation loop with batch processing
        const validationConfig = {
          mandatoryAttributes,
          collabSpaceTitles,
          matchedOperation,
          invalidPrefixes: ["MMI-", "RS-", "DAN-", "RSC-", "TF-", "ROXA-"],
        };

        console.log("Starting batch validation process...");
        const validationErrors = await validateDataInBatches(
          sheetData,
          validationConfig
        );

        if (validationErrors.length > 0) {
          console.log("Validation completed with errors:", validationErrors);
          reject({
            errors: validationErrors,
            headers,
            mandatoryAttributes,
          });
          return;
        }

        // ✅ **Final Step: Resolve with Headers and Sheet Data**
        console.log("✅ File validation successful. No errors found.");
        resolve({
          headers,
          sheetData,
          validationErrors: [],
          mandatoryAttributes, // Make sure we're returning mandatoryAttributes
        });
      } catch (error) {
        console.error("Error during file validation:", error);
        reject({
          errors: ["An error occurred while processing the file."],
          headers: [],
        });
      }
    };

    // ❌ **Handle File Read Errors**

    reader.onerror = () => {
      console.error("❌ Error reading the file.");
      reject({ errors: ["File reading failed."], headers: [] });
    };
  });
};

export default validateFile;
