export const validateDataInBatches = async (sheetData, validationConfig) => {
  try {
    // Create new worker instance with proper configuration
    const worker = new Worker(
      new URL('./Webworker.js', import.meta.url),
      { type: 'module' }
    );

    return new Promise((resolve, reject) => {
      worker.onmessage = ({ data }) => {
        switch (data.type) {
          case 'progress':
            window.dispatchEvent(
              new CustomEvent('validation-progress', { 
                detail: { progress: data.progress } 
              })
            );
            break;
            
          case 'complete':
            console.log('[validateDataInBatches] Validation complete');
            worker.terminate();
            resolve(data.errors);
            break;
            
          case 'error':
            console.error('[validateDataInBatches] Worker error:', data.error);
            worker.terminate();
            reject(new Error(data.error));
            break;
        }
      };

      worker.onerror = (error) => {
        console.error('[validateDataInBatches] Worker error:', error);
        worker.terminate();
        reject(error);
      };

      // Start validation process
      console.log('[validateDataInBatches] Starting validation with worker');
      worker.postMessage({ data: sheetData, validationConfig });
    });
  } catch (error) {
    console.error('[validateDataInBatches] Error creating worker:', error);
    throw error;
  }
};